
.nav-title {
  float: left;
}
.navbar-brand {
  height: 50px;
  padding: 15px 15px;
  font-size: 18px;
  line-height: 20px;
}
.nav-buttons {
  float: right;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 12px;
  padding-bottom: 13px;
}
.navbar-header {
  width: 100%;
}
.nav-buttons .awe-button {
  color: white;
  cursor: pointer;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
  line-height: 25px;
  vertical-align: middle;
  border-right: 1px solid rgba(255,255,255,0.1);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.nav-buttons .awe-button:last-of-type {
  border-right: 0px solid;
}
.nav-buttons .awe-button:hover {
  color: #ccc;
}
.nav-buttons .awe-button .profile-img {
  width: 25px;
  height: 25px;
  display: inline-block;
  margin-right: 5px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
}



