
.awe-dashboard-card-widget-mask {
  width: 100%;
  height: 100%;
}
.awe-dashboard-card-widget-mask.display-bg-image {
  opacity: 0;
}
.awe-dashboard-card-widget-mask.display-bg-image:hover {
  opacity: 1;
  background-color: rgba(0,0,0,0.85)
}
.icon-image {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.awe-dashboard-card-follow-button {
  float: right;
  color: #fff;
  font-size: 10px;
  background-color: #c81360;
  text-transform: capitalize;
  margin-top: 2px;
  cursor: pointer;
}
.awe-dashboard-card-follow-button:hover {
  background-color: #b32d8e;
}
.awe-dashboard-card-container {
  background-color: #1c2232;
  height: 200px;
  margin-left: 10px;
  margin-top: 10px;
  float: left;
  /*border: 1px solid #070A07;*/
  -webkit-box-shadow: 1px 1px 1px 0px #070A07;
          box-shadow: 1px 1px 1px 0px #070A07;
}
.awe-dashboard-card-header {
  background-color: #262e44;
  text-align: left;
  padding: 3px;
  padding-left: 5px;
  padding-right: 5px;
  color: white;
  text-transform: uppercase;
  font-weight: 700;
  display: flow-root;
}
.awe-dashboard-card-widget {
  text-align: left;
  padding: 7px;
  color: #cccccc;
  border-bottom: 1px solid #161a25;
}
.awe-dashboard-card-footer {
  background-color: #1c2232;
  text-align: left;
  padding: 3px;
  padding-left: 6px;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.awe-dashboard-card-container:hover .awe-dashboard-card-footer {
  background-color: #1c2232;
}
.awe-dashboard-card-container:hover .awe-dashboard-card-body {
  background-color: #333;
}
.awe-dashboard-card-container:hover .awe-dashboard-card-tags {
  background-color: #333;
}
.awe-dashboard-card-tags {
  margin-right: 10px;
  font-size: 10px;
  background-color: #333;
  color: #ccc;
}
.awe-dashboard-card-body {
  overflow-y: auto;
  height: calc(100% - 52px);
  cursor: pointer;
}
.awe-dashboard-card-body::-webkit-scrollbar {
  width: 5px;
  height: 12px;
}
.awe-dashboard-card-body::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}
.awe-dashboard-card-body::-webkit-scrollbar-thumb {
  background: #555;
  visibility: hidden;
}
.awe-dashboard-card-body:hover::-webkit-scrollbar-thumb {
  visibility: visible;
}



